/*******************************
         Activity Feed
*******************************/
.ui.feed {
  margin: 1em 0;
}
.ui.feed:first-child {
  margin-top: 0;
}
.ui.feed:last-child {
  margin-bottom: 0;
}
/*******************************
            Content
*******************************/
/* Event */
.ui.feed > .event {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 0.21428571rem 0;
  margin: 0;
  background: none;
  border-top: none;
}
.ui.feed > .event:first-child {
  border-top: 0;
  padding-top: 0;
}
.ui.feed > .event:last-child {
  padding-bottom: 0;
}
/* Event Label */
.ui.feed > .event > .label {
  display: block;
  flex: 0 0 auto;
  width: 2.5em;
  height: auto;
  align-self: stretch;
  text-align: left;
}
.ui.feed > .event > .label .icon {
  opacity: 1;
  font-size: 1.5em;
  width: 100%;
  padding: 0.25em;
  background: none;
  border: none;
  border-radius: none;
  color: rgba(0, 0, 0, 0.6);
}
.ui.feed > .event > .label img {
  width: 100%;
  height: auto;
  border-radius: 500rem;
}
.ui.feed > .event > .label + .content {
  margin: 0.5em 0 0.35714286em 1.14285714em;
}
/*--------------
     Content
---------------*/
/* Content */
.ui.feed > .event > .content {
  display: block;
  flex: 1 1 auto;
  align-self: stretch;
  text-align: left;
  word-wrap: break-word;
}
.ui.feed > .event:last-child > .content {
  padding-bottom: 0;
}
/* Link */
.ui.feed > .event > .content a {
  cursor: pointer;
}
/*--------------
      Date
---------------*/
.ui.feed > .event > .content .date {
  margin: -0.5rem 0 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.4);
  font-weight: normal;
  font-size: 1em;
  font-style: normal;
}
/*--------------
     Summary
---------------*/
.ui.feed > .event > .content .summary {
  margin: 0;
  font-size: 1em;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.87);
}
/* Summary Image */
.ui.feed > .event > .content .summary img {
  display: inline-block;
  width: auto;
  height: 10em;
  margin: -0.25em 0.25em 0 0;
  border-radius: 0.25em;
  vertical-align: middle;
}
/*--------------
      User
---------------*/
.ui.feed > .event > .content .user {
  display: inline-block;
  font-weight: bold;
  margin-right: 0;
  vertical-align: baseline;
}
.ui.feed > .event > .content .user img {
  margin: -0.25em 0.25em 0 0;
  width: auto;
  height: 10em;
  vertical-align: middle;
}
/*--------------
   Inline Date
---------------*/
/* Date inside Summary */
.ui.feed > .event > .content .summary > .date {
  display: inline-block;
  float: none;
  font-weight: normal;
  font-size: 0.85714286em;
  font-style: normal;
  margin: 0 0 0 0.5em;
  padding: 0;
  color: rgba(0, 0, 0, 0.4);
}
/*--------------
  Extra Summary
---------------*/
.ui.feed > .event > .content .extra {
  margin: 0.5em 0 0;
  background: none;
  padding: 0;
  color: rgba(0, 0, 0, 0.87);
}
/* Images */
.ui.feed > .event > .content .extra.images img {
  display: inline-block;
  margin: 0 0.25em 0 0;
  width: 6em;
}
/* Text */
.ui.feed > .event > .content .extra.text {
  padding: 0;
  border-left: none;
  font-size: 1em;
  max-width: 500px;
  line-height: 1.4285em;
}
/*--------------
      Meta
---------------*/
.ui.feed > .event > .content .meta {
  display: inline-block;
  font-size: 0.85714286em;
  margin: 0.5em 0 0;
  background: none;
  border: none;
  border-radius: 0;
  box-shadow: none;
  padding: 0;
  color: rgba(0, 0, 0, 0.6);
}
.ui.feed > .event > .content .meta > * {
  position: relative;
  margin-left: 0.75em;
}
.ui.feed > .event > .content .meta > *:after {
  content: '';
  color: rgba(0, 0, 0, 0.2);
  top: 0;
  left: -1em;
  opacity: 1;
  position: absolute;
  vertical-align: top;
}
.ui.feed > .event > .content .meta .like {
  color: '';
  transition: 0.2s color ease;
}
.ui.feed > .event > .content .meta .like:hover i.icon {
  color: #FF2733;
}
.ui.feed > .event > .content .meta .active.like i.icon {
  color: #EF404A;
}
/* First element */
.ui.feed > .event > .content .meta > :first-child {
  margin-left: 0;
}
.ui.feed > .event > .content .meta > :first-child::after {
  display: none;
}
/* Action */
.ui.feed > .event > .content .meta a,
.ui.feed > .event > .content .meta > i.icon {
  cursor: pointer;
  opacity: 1;
  color: rgba(0, 0, 0, 0.5);
  transition: color 0.1s ease;
}
.ui.feed > .event > .content .meta a:hover,
.ui.feed > .event > .content .meta a:hover i.icon,
.ui.feed > .event > .content .meta > i.icon:hover {
  color: rgba(0, 0, 0, 0.95);
}
/*******************************
            Variations
*******************************/
.ui.feed {
  font-size: 1rem;
}
.ui.mini.feed {
  font-size: 0.78571429rem;
}
.ui.tiny.feed {
  font-size: 0.85714286rem;
}
.ui.small.feed {
  font-size: 0.92857143rem;
}
.ui.large.feed {
  font-size: 1.14285714rem;
}
.ui.big.feed {
  font-size: 1.28571429rem;
}
.ui.huge.feed {
  font-size: 1.42857143rem;
}
.ui.massive.feed {
  font-size: 1.71428571rem;
}
/*------------------
        Inverted
  -------------------*/
.ui.inverted.feed > .event {
  background: #1B1C1D;
}
.ui.inverted.feed > .event > .content .date,
.ui.inverted.feed > .event > .content .meta .like {
  color: rgba(255, 255, 255, 0.7);
}
.ui.inverted.feed > .event > .content .summary,
.ui.inverted.feed > .event > .content .extra.text {
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.feed > .event > .content .meta .like:hover {
  color: #ffffff;
}
