/*--------------
      Item
---------------*/
.ui.items > .item {
  display: flex;
  margin: 1em 0;
  width: 100%;
  min-height: 0;
  background: transparent;
  padding: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  transition: box-shadow 0.1s ease;
  z-index: '';
}
.ui.items > .item a {
  cursor: pointer;
}
/*--------------
      Items
---------------*/
.ui.items {
  margin: 1.5em 0;
}
.ui.items:first-child {
  margin-top: 0 !important;
}
.ui.items:last-child {
  margin-bottom: 0 !important;
}
/*--------------
      Item
---------------*/
.ui.items > .item:after {
  display: block;
  content: ' ';
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.ui.items > .item:first-child {
  margin-top: 0;
}
.ui.items > .item:last-child {
  margin-bottom: 0;
}
/*--------------
     Images
---------------*/
.ui.items > .item > .image {
  position: relative;
  flex: 0 0 auto;
  display: block;
  float: none;
  margin: 0;
  padding: 0;
  max-height: '';
  align-self: start;
}
.ui.items > .item > .image > img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 0.125rem;
  border: none;
}
.ui.items > .item > .image:only-child > img {
  border-radius: 0;
}
/*--------------
     Content
---------------*/
.ui.items > .item > .content {
  display: block;
  flex: 1 1 auto;
  background: none;
  color: rgba(0, 0, 0, 0.87);
  margin: 0;
  padding: 0;
  box-shadow: none;
  font-size: 1em;
  border: none;
  border-radius: 0;
}
.ui.items > .item > .content:after {
  display: block;
  content: ' ';
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
.ui.items > .item > .image + .content {
  min-width: 0;
  width: auto;
  display: block;
  margin-left: 0;
  align-self: start;
  padding-left: 1.5em;
}
.ui.items > .item > .content > .header {
  display: inline-block;
  margin: -0.21425em 0 0;
  font-family: 'Lato', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
}
/* Default Header Size */
.ui.items > .item > .content > .header:not(.ui) {
  font-size: 1.28571429em;
}
/*--------------
     Floated
---------------*/
.ui.items > .item [class*="left floated"] {
  float: left;
}
.ui.items > .item [class*="right floated"] {
  float: right;
}
/*--------------
  Content Image
---------------*/
.ui.items > .item .content img {
  align-self: center;
  width: '';
}
.ui.items > .item img.avatar,
.ui.items > .item .avatar img {
  width: '';
  height: '';
  border-radius: 500rem;
}
/*--------------
   Description
---------------*/
.ui.items > .item > .content > .description {
  margin-top: 0.6em;
  max-width: auto;
  font-size: 1em;
  line-height: 1.4285em;
  color: rgba(0, 0, 0, 0.87);
}
/*--------------
    Paragraph
---------------*/
.ui.items > .item > .content p {
  margin: 0 0 0.5em;
}
.ui.items > .item > .content p:last-child {
  margin-bottom: 0;
}
/*--------------
      Meta
---------------*/
.ui.items > .item .meta {
  margin: 0.5em 0 0.5em;
  font-size: 1em;
  line-height: 1em;
  color: rgba(0, 0, 0, 0.6);
}
.ui.items > .item .meta * {
  margin-right: 0.3em;
}
.ui.items > .item .meta :last-child {
  margin-right: 0;
}
.ui.items > .item .meta [class*="right floated"] {
  margin-right: 0;
  margin-left: 0.3em;
}
/*--------------
      Links
---------------*/
/* Generic */
.ui.items > .item > .content a:not(.ui) {
  color: '';
  transition: color 0.1s ease;
}
.ui.items > .item > .content a:not(.ui):hover {
  color: '';
}
/* Header */
.ui.items > .item > .content > a.header {
  color: rgba(0, 0, 0, 0.85);
}
.ui.items > .item > .content > a.header:hover {
  color: #1e70bf;
}
/* Meta */
.ui.items > .item .meta > a:not(.ui) {
  color: rgba(0, 0, 0, 0.4);
}
.ui.items > .item .meta > a:not(.ui):hover {
  color: rgba(0, 0, 0, 0.87);
}
/*--------------
     Labels
---------------*/
/*-----Star----- */
/* Icon */
.ui.items > .item > .content .favorite.icon {
  cursor: pointer;
  opacity: 0.75;
  transition: color 0.1s ease;
}
.ui.items > .item > .content .favorite.icon:hover {
  opacity: 1;
  color: #FFB70A;
}
.ui.items > .item > .content .active.favorite.icon {
  color: #FFE623;
}
/*-----Like----- */
/* Icon */
.ui.items > .item > .content .like.icon {
  cursor: pointer;
  opacity: 0.75;
  transition: color 0.1s ease;
}
.ui.items > .item > .content .like.icon:hover {
  opacity: 1;
  color: #FF2733;
}
.ui.items > .item > .content .active.like.icon {
  color: #FF2733;
}
/*----------------
  Extra Content
-----------------*/
.ui.items > .item .extra {
  display: block;
  position: relative;
  background: none;
  margin: 0.5rem 0 0;
  width: 100%;
  padding: 0 0 0;
  top: 0;
  left: 0;
  color: rgba(0, 0, 0, 0.4);
  box-shadow: none;
  transition: color 0.1s ease;
  border-top: none;
}
.ui.items > .item .extra > * {
  margin: 0.25rem 0.5rem 0.25rem 0;
}
.ui.items > .item .extra > [class*="right floated"] {
  margin: 0.25rem 0 0.25rem 0.5rem;
}
.ui.items > .item .extra:after {
  display: block;
  content: ' ';
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}
/*******************************
          Responsive
*******************************/
/* Default Image Width */
.ui.items > .item > .image:not(.ui) {
  width: 175px;
}
/* Tablet Only */
@media only screen and (min-width: 768px) and (max-width: 991.98px) {
  .ui.items > .item {
    margin: 1em 0;
  }
  .ui.items > .item > .image:not(.ui) {
    width: 150px;
  }
  .ui.items > .item > .image + .content {
    display: block;
    padding: 0 0 0 1em;
  }
}
/* Mobile Only */
@media only screen and (max-width: 767.98px) {
  .ui.items:not(.unstackable) > .item {
    flex-direction: column;
    margin: 2em 0;
  }
  .ui.items:not(.unstackable) > .item > .image {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .ui.items:not(.unstackable) > .item > .image,
  .ui.items:not(.unstackable) > .item > .image > img {
    max-width: 100% !important;
    width: auto !important;
    max-height: 250px !important;
  }
  .ui.items:not(.unstackable) > .item > .image + .content {
    display: block;
    padding: 1.5em 0 0;
  }
}
/*******************************
           Variations
*******************************/
/*-------------------
         Aligned
  --------------------*/
.ui.items > .item > .image + [class*="top aligned"].content {
  align-self: flex-start;
}
.ui.items > .item > .image + [class*="middle aligned"].content {
  align-self: center;
}
.ui.items > .item > .image + [class*="bottom aligned"].content {
  align-self: flex-end;
}
/*--------------
       Relaxed
  ---------------*/
.ui.relaxed.items > .item {
  margin: 1.5em 0;
}
.ui[class*="very relaxed"].items > .item {
  margin: 2em 0;
}
/*-------------------
        Divided
  --------------------*/
.ui.divided.items > .item {
  border-top: 1px solid rgba(34, 36, 38, 0.15);
  margin: 0;
  padding: 1em 0;
}
.ui.divided.items > .item:first-child {
  border-top: none;
  margin-top: 0 !important;
  padding-top: 0 !important;
}
.ui.divided.items > .item:last-child {
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
}
/* Relaxed Divided */
.ui.relaxed.divided.items > .item {
  margin: 0;
  padding: 1.5em 0;
}
.ui[class*="very relaxed"].divided.items > .item {
  margin: 0;
  padding: 2em 0;
}
/*-------------------
          Link
  --------------------*/
.ui.items a.item:hover,
.ui.link.items > .item:hover {
  cursor: pointer;
}
.ui.items a.item:hover .content .header,
.ui.link.items > .item:hover .content .header {
  color: #1e70bf;
}
/*--------------
      Size
---------------*/
.ui.items > .item {
  font-size: 1em;
}
.ui.mini.items > .item {
  font-size: 0.78571429em;
}
.ui.tiny.items > .item {
  font-size: 0.85714286em;
}
.ui.small.items > .item {
  font-size: 0.92857143em;
}
.ui.large.items > .item {
  font-size: 1.14285714em;
}
.ui.big.items > .item {
  font-size: 1.28571429em;
}
.ui.huge.items > .item {
  font-size: 1.42857143em;
}
.ui.massive.items > .item {
  font-size: 1.71428571em;
}
/*---------------
     Unstackable
  ----------------*/
@media only screen and (max-width: 767.98px) {
  .ui.unstackable.items > .item > .image,
  .ui.unstackable.items > .item > .image > img {
    width: 125px !important;
  }
}
/*--------------
       Inverted
  ---------------*/
.ui.inverted.items > .item {
  background: transparent;
}
.ui.inverted.items > .item > .content {
  background: none;
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.items > .item .extra {
  background: none;
}
.ui.inverted.items > .item > .content > .header {
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.items > .item > .content > .description {
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.items > .item .meta {
  color: rgba(255, 255, 255, 0.8);
}
.ui.inverted.items > .item > .content a:not(.ui) {
  color: #57a4ef;
}
.ui.inverted.items > .item > .content a:not(.ui):hover {
  color: #4183C4;
}
.ui.inverted.items > .item > .content > a.header {
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.items > .item > .content > a.header:hover {
  color: #ffffff;
}
.ui.inverted.items > .item .meta > a:not(.ui) {
  color: rgba(255, 255, 255, 0.7);
}
.ui.inverted.items > .item .meta > a:not(.ui):hover {
  color: rgba(255, 255, 255, 0.9);
}
.ui.inverted.items > .item > .content .favorite.icon:hover {
  color: #ffc63d;
}
.ui.inverted.items > .item > .content .active.favorite.icon {
  color: #ffec56;
}
.ui.inverted.items > .item > .content .like.icon:hover {
  color: #ff5a63;
}
.ui.inverted.items > .item > .content .active.like.icon {
  color: #ff5a63;
}
.ui.inverted.items > .item .extra {
  color: rgba(255, 255, 255, 0.7);
}
.ui.inverted.items a.item:hover .content .header,
.ui.inverted.link.items > .item:hover .content .header {
  color: #ffffff;
}
.ui.inverted.divided.items > .item {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.ui.inverted.divided.items > .item:first-child {
  border-top: none;
}
